<!---->
<style lang='scss' rel='stylesheet/scss'>
    @import "../../../utils/mixins/common";
    @import "../../../utils/mixins/mixins";

    .create-course-task {
        @extend %content-container;
        background: #fff;
        padding: 20px;
        .el-form {
            width: 60%;
            img {
                width: 35%;
                // height: 50% 
            }
        }
        /*.search {
            @extend %top-search-container;
        }*/
        .collection {
            align-items: center;
            min-height: 36px;
            border-radius: 4px;
            padding: 3px 30px 3px 10px;
            border: 1px solid #bfcbd9;
        }
       
        .u-course-tag {
            margin-right: 10px;
            background-color: rgba(32,160,255,.1);
            display: inline-block;
            padding: 0 10px;
            height: 32px;
            line-height: 30px;
            font-size: 12px;
            color: #20a0ff;
            border-radius: 4px;
            box-sizing: border-box;
            border: 1px solid rgba(32,160,255,.2);
            white-space: nowrap;
            &:last-child {
                margin-right: 0;
            }
        }
        .el-tabs__content {
            position: relative;
            .dialog-select-item {
                h5 {
                    line-height: 40px;
                    font-size: 14px;
                    padding-left: 20px;
                    background: #fbfdff;
                    border: 1px solid #d1dbe5;
                    border-bottom: none;
                }
                position: absolute;
                top: 54px;
                right: 120px;
                height: 500px;
                width: 42%;
                display: inline-block;
                vertical-align: top;
            }
        }
        .row-class {
            border: 1px solid #d1dbe5;
        }

        .course-search {
            margin-bottom: 12px;
            .el-input {
                width: 150px;
                ::-webkit-input-placeholder {
                    font-size: 13px
                }
            }
        }
    }
</style>

<template>
    <article class="create-course-task">
        <el-form :model="form" :rules="rules" label-position="right" ref="form" label-width="120px" style="width: 60%">
            
            <el-form-item prop="title" label="">
                <el-input v-model="form.title" auto-complete="off"></el-input>
                
            </el-form-item>

            <el-form-item prop="description" label="">
                <el-input v-model="form.description" auto-complete="off" :rows="6" type="textarea"></el-input>
            </el-form-item>
            <el-form-item prop="imgUrl" label="">
                <div class="img-wrap" v-if="form.image">
                    <img :src="form.image | fillImgPath" alt=""/>
                </div>
                <!--<ImagEcropperInput :isRound="false" :aspectRatio="2.15" :confirmFn="cropperFn"-->
                <ImagEcropperInput :isRound="false" :confirmFn="cropperFn"
                                   class="upload-btn"></ImagEcropperInput>
            </el-form-item>
            <el-form-item prop="course" label="">
                <el-tag style="margin-right: 3px"
                        v-for="(c,index) in form.course" :key="index"
                        :closable="true"
                        @close="form.course.splice(index,1)"
                        type="success">
                    {{c.course_name}}
                </el-tag>
                <el-button type="primary" @click="dialogCourse.isShow=true" size="small"></el-button>
            </el-form-item>
            <el-form-item prop="sort" label="">
                <el-input-number v-model="form.sort" auto-complete="off"></el-input-number>
            </el-form-item>
            <el-form-item label="">
                <DateRange :start="form.stime" :end="form.etime" @changeStart="val=> form.stime=val "
                    @changeEnd="val=> form.etime=val" >
                </DateRange>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select clearable v-model="form.type" @change="choosePushType" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item 
                :label="pushTypeDialog.title" 
                v-if="form.type && form.type===pushTypeDialog.type">
                <div class="collection" @click="openPushTypeDialog">
                    <el-tag 
                        class="u-course-tag"
                        v-for="item in pushTypeDialog.selectedData[this.pushTypeDialog.type]" 
                        :key="item.id">
                        {{item.name}}
                    </el-tag>
                </div>
            </el-form-item>
            <el-form-item label="">
                <el-input style="width: auto;" v-model.number="form.score" type="number"  placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit(0)"></el-button>
                <!--<el-button type="warning" @click="submit(1)"></el-button>-->
            </el-form-item>
        </el-form>

        <!--  -->
        <dialogSelectData ref="dialogSelect" v-model="dialogCourse.isShow" :getData="fetchCourse" title=""
                          :selectedList="form.course" @changeSelected="val=>form.course=val">
            <div slot="search" class="course-search">
                <el-input @keyup.enter.native="$refs.dialogSelect.fetchCourse(true)" v-model="dialogCourse.course_name"
                          icon="search"
                          placeholder=""></el-input>
            </div>
        </dialogSelectData>
        <!--  -->
        <el-dialog 
            :title="pushTypeDialog.title" 
            :visible.sync="pushTypeDialog.showDialog" 
            v-if="pushTypeDialog.showDialog">
            <template v-if="pushTypeDialog.isSearch">
                <section class="search">
                    <section>
                        <i></i>
                        <DepSelect v-model="pushTypeDialog.fetchParam.gov_ids" :change="getPushTypeData"></DepSelect>
                    </section>
                </section>
            </template>
            <Transfer placeholder=""
                      @searchFn="(val)=>{pushTypeDialog.page=1;pushTypeDialog.fetchParam.name=val;fetchPushTypeData();}"
                      @moreFn="()=>{pushTypeDialog.page++;fetchPushTypeData('no-clear');}" 
                      :total="pushTypeDialog.total"
                      :data="pushTypeDialog.data"
                      :selectedValue='selectData'
                      v-model="pushTypeDialog.selectedData[pushTypeDialog.type]"></Transfer>
            <span slot="footer" class="dialog-footer">
                <el-button @click="pushTypeDialog.showDialog = false"> </el-button>
                <el-button type="primary" @click="transferConfirmFn"> </el-button>
            </span>
        </el-dialog>

    </article>
</template>

<script>
    import Transfer from '../../component/dialog/Transfer.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInput.vue'
    import courseTaskService from '../../../services/gov/courseTaskService.js'
    import courseService from '../../../services/course/courseService.js'
    import commonService from '../../../services/commonService.js'
    import govService from '../../../services/gov/govService.js'
    import userService from '../../../services/gov/userService.js'
    import dialogSelectData from '../../component/dialog/SelectData4table.vue'
    import DateRange from '../../component/form/DateRangePicker.vue'
    import DepSelect from '../../component/select/Department.vue'


    export default{
        name: 'server-manage-add',
        computed: {
            id () {
                return this.$route.query.id
            },
        },
        data () {
            return {
                selectData:[],
                form: {                // 
                    id: void 0,
                    title: void 0,          // 
                    // category_id: void 0,       // 
                    image: void 0,        // 
                    description: void 0,  // 
                    sort: void 0,         // 
                    course_ids: void 0,     // 
                    gov_ids: void 0,     // 
                    user_ids: void 0,     // 
                    // status: void 0,       // 
                    course: [],
                    score: 0,     // 
                    type:void 0,       // 
                    stime:'',
                    etime:'',

                },
                rules: {
                    title: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    category_id: {type: 'number', required: true, message: '', trigger: 'change'}
                },
                dialogCourse: {
                    loading: false,
                    isShow: false,
                    course_name: void 0,
                },
                pushTypeDialog: { //
                    fetchParam: {
                        gov_ids: '',
                        name: ''
                    },
                    title: '',
                    isSearch: '',
                    type: '',
                    showDialog: false,
                    selectedData: {
                        2: [],
                        1: []
                    },
                    data: [],
                    page: 1,
                    pagesize: 15,
                    total: 0,
                },
            }
        },
        watch:{
            'form.type'(){
                if(this.form.type==1){//
                    this.form.user_ids= null
                }else{ //
                    this.form.gov_ids= null
                }
            },
        },
        created () {
            console.log('(this.pushTypeDialog.selectedData')
           console.log(this.pushTypeDialog.selectedData[this.pushTypeDialog.type])

            xmview.setContentLoading(false)
            if (this.$route.query.item) {
                this.form = this.$route.query.item
                xmview.setContentTile('')
                this.choosePushType()
            } else if (this.id === undefined) {
                xmview.setContentLoading(false)
            } else {
                courseTaskService.courseTaskDetail({id: this.id}).then((ret) => {
                    this.form = Object.assign(this.form, ret.data)
                    // this.form.end_time = this.form.end_day
                    this.selectCourseAndExam = ret.object
                    ret.object.forEach(o => {
                        if (o.type === 'course') {
                            this.course.selectCourse.push(o)
                        } else if (o.type === 'exam') {
                            this.exam.selectExam.push(o)
                        }
                    })
                    this.choosePushType()
                    this.pushTypeDialog.selectedData[this.pushTypeDialog.type] = this.generatorList(ret.push_type_list || [])
                    xmview.setContentLoading(false)
                })
            }

            this.pushTypeDialog.selectedData[this.pushTypeDialog.type] = []
        },
        methods: {
            transferConfirmFn () {
                this.pushTypeDialog.showDialog = false
            },
            //
            openPushTypeDialog () {
                this.pushTypeDialog.showDialog = true
                this.pushTypeDialog.page = 1
                this.pushTypeDialog.fetchParam.gov_ids = ''
                this.pushTypeDialog.fetchParam.name = ''
                this.fetchPushTypeData()
            },
            //
            choosePushType(){
                let map = {
                    1: {
                        type: '1',
                        label: '',
                        isSearch: false,
                    },
                    2: {
                        type: '2',
                        label: '',
                        isSearch: true,
                    }
                }
                let param = map[this.form.type]
                // console.log(param)
                if(param!=undefined){
                    this.pushTypeDialog.title = param.label
                    this.pushTypeDialog.isSearch = param.isSearch
                    this.pushTypeDialog.type = param.type
                    // console.log(this.pushTypeDialog.type)
                }
                
            },
            getPushTypeData () {
                this.pushTypeDialog.page = 1
                this.fetchPushTypeData()
            },
            fetchPushTypeData (type) {
                type !== 'no-clear' && (this.pushTypeDialog.data = [])
                let map = {
                    1: govService.getSelectList,  //
                    2: userService.fetchData,  // 
                    // user_group: userService.userGroupSearch
                }
                let param = {
                    name: this.pushTypeDialog.fetchParam.name,
                    page: this.pushTypeDialog.page,
                    pagesize: this.pushTypeDialog.pagesize
                }
                
                if (this.pushTypeDialog.isSearch) {
                    param.gov_ids = this.pushTypeDialog.fetchParam.gov_ids
                    param.role_id = -1
                }
                map[this.pushTypeDialog.type](param).then(ret => {
                    // this.pushTypeDialog.total = ret._exts.total
                    this.pushTypeDialog.total = 690
                    // if (this.pushTypeDialog.data.length > 0 && this.pushTypeDialog.data.length < ret._exts.total) {
                    if (this.pushTypeDialog.data.length > 0 ) {
                        this.pushTypeDialog.data.splice(-1, 1)
                    }
                    this.pushTypeDialog.data.push(...ret.data, {id: -1}) //id:-1 
                    // this.pushTypeDialog.data.push(...ret.data) //id:-1 
                    //  this.pushTypeDialog.data.forEach(item => { item.type = 'exam' })
                })
            },
            generatorList (arr) {
                return arr.map(item => {
                    return {
                        id: item['department_id'] || item['user_id'] || item['group_id'],
                        name: item['department_name'] || item['user_name'] || item['group_name']
                    }
                })
            },
            cropperFn(data, ext) {
                commonService.commonUploadImageBaseSection({
                    image: data,
                    alias: `${Date.now()}${ext}`
                }).then((ret) => {
                    xmview.showTip('success', '')
                    this.form.image = ret.url // 
                })
            },
            fetchCourse (params) {
                // { course_name = '', status, category_id , time_start, time_end, page, pagesize}
                return courseService.getPublicCourselist(Object.assign({}, this.dialogCourse, params))
            },
            submit(s) {
                this.$refs.form.validate((valid) => {
                    if (!valid) {
                        return false
                    }
                    // id
                    this.form.course_ids = []
                    // this.form.course.forEach((c) => {
                    //     this.form.course_ids.push(c.id)
                    // })
                      this.form.course.forEach((c) => {
                        this.form.course_ids.push(c.contentid)
                    })
                    this.form.course_ids = this.form.course_ids.join(',')

                    
                    if(this.form.type==1){
                        // govids
                        this.form.gov_ids = this.pushTypeDialog.type && this.pushTypeDialog.selectedData[this.pushTypeDialog.type].map(item => {
                        return item.id
                        }).join(',')
                    }else{
                        // userids
                        this.form.user_ids = this.pushTypeDialog.type && this.pushTypeDialog.selectedData[this.pushTypeDialog.type].map(item => {
                        return item.id
                        }).join(',')
                    }
                    
                    
                    // this.fetchParam.end_time = this.timeFormatter(this.fetchParam.end_time, true)

                    if (s > 0) { //
                        this.form.status = s
                    }
                    let reqFn = courseTaskService.submitTask
                    if (this.form.id) {
                        reqFn = courseTaskService.editTask
                    }
                    reqFn(this.form).then((ret) => {
                        xmview.showTip('success', '')
                        this.$router.back()
                    }).catch((ret) => {
                        xmview.showTip('error', ret.message)
                    })
                })
            }
        },
        components: {DateRange,ImagEcropperInput, dialogSelectData, Transfer, DepSelect }
    }
</script>



